.header__container {
  margin-left: auto;
  margin-right: auto;
  padding: 0 48px;
  max-width: 1360px;
  padding-top: 5px;
}

.header__link {
  position: relative;
  color: inherit;
  text-decoration: none;
  padding-bottom: 16px;
  padding-top: 16px;
  display: block;
  transition: color var(--time) var(--cubic);

  &:hover,
  &:focus {
    color: var(--button-active-green);
  }

  &::after,
  &::after {
    content: '';
    position: absolute;
    left: 0;
    bottom: 8px;
    width: 100%;
    height: 2px;
    border-radius: 1px;
    background-color: var(--button-active-green);
    transition: transform var(--time);
    transform: scaleX(0);
    transform-origin: right;
  }

  &:hover::after,
  &:focus::after {
    transform-origin: left;
    transform: scaleX(1);
  }
}

.header {
  width: 100%;
  position: absolute;
  left: 0;
  top: 0;
  padding-top: 21px;
}

.logo {
  display: block;
  font-size: 32px;
  line-height: calc(42 / 32);
  display: flex;
  align-items: center;
  color: $secondary-color;
  margin-bottom: 10px;
  text-decoration: none;
  justify-content: center;
}
.logo__icon {
  display: block;
  fill: $secondary-bg-color;
  margin-right: 6px;
  min-width: 40px;
  min-height: 35px;
}
.icon__container {
}
.logo__span {
  display: block;
  color: $secondary-bg-color;
}
.header__list {
  @extend %list;
  display: flex;
  gap: 13px;

  font-size: 16px;
  line-height: calc(26 / 16);
  color: $secondary-color;
  justify-content: center;
}
.header__list-item {
}

