@media screen and (min-width: 768px) {
  .header {
    padding-top: 0;
  }
  .header__nav {
    display: flex;
    gap: 65px;
    justify-content: space-between;
  }
  .logo {
    font-size: 35px;
    line-height: calc(42 / 32);
    padding-top: 6px;
    padding-bottom: 6px;
    margin-bottom: 0;
  }
  .logo__icon {
    margin-right: 10px;
  }
  .logo__span {
    margin-right: 10px;
  }
  .header__list {
    align-items: center;
    gap: 19px;
    font-size: 18px;
    line-height: calc(28 / 18);
  }
  .heder__link {
    padding-bottom: 16px;
    padding-top: 16px;
  }
}

@media screen and (min-width: 1360px) {
  .header__nav {
    display: flex;
    justify-content: space-between;
  }
  .header__list {
    gap: 38px;
  }
}
