// @import '~node_modules/modern-normalize/modern-normalize.css';
:root {
  --primary-color: #333333;
  --secondary-color: #fff;

  --primary-bg-color: #fff;
  --secondary-bg-color: #28a745;
  --third-bg-color: #f4f4f4;

  --form-color: #808080;
  --titlle-hero-color: #e9e9e9;

  --bg-color-blog-section: #0284d0;
  --shadow-btn: 0px 3px 1px rgba(0, 0, 0, 0.1), 0px 1px 2px rgba(0, 0, 0, 0.08),
    0px 2px 2px rgba(0, 0, 0, 0.12);

  --button-active-blue: #0376b8;
  --button-active-green: #23913d;

  /* Переменные для анимации */
  --time: 350ms;
  --cubic: ease;
}

body {
  font-family: 'Open Sans', sans-serif;
  font-size: 18px;
  line-height: 1.56;

  /* or 156% */
  color: var(--primary-color);

  background-color: var(--primary-bg-color);
}

.visually-hidden {
  position: absolute;
  white-space: nowrap;
  width: 1px;
  height: 1px;
  overflow: hidden;
  border: 0;
  padding: 0;
  clip: rect(0 0 0 0);
  clip-path: inset(50%);
  margin: -1px;
}

img {
  display: block;
  max-width: 100%;
  height: auto;
}

svg {
  max-width: 100%;
  height: auto;
}

.list {
  list-style: none;
  margin: 0;
  padding: 0;
}

.container {
  margin: 0 auto;

  @media (min-width: 320px) {
    width: 320px;
  }

  @media (min-width: 768px) {
    width: 768px;
  }

  @media (min-width: 1360px) {
    width: 1360px;
  }
  // margin-left: auto;
  // margin-right: auto;
  // max-width: 1360px;
}

.section-title {
  margin: 0;
  margin-bottom: 34px;

  font-weight: 300;
  font-size: 38px;
  line-height: 1.26;
}

.section-title-center {
  text-align: center;
}

.modal {
  position: absolute;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 300px;
  border-radius: 5px;
  z-index: 52;
  overflow: scroll;
  top: 30px;
  left: 50%;
  transform: translateX(-50%);
  background-color: rgb(255, 253, 192);
  height: 480px;
  visibility: hidden;
  opacity: 0;
  pointer-events: none;

  transition: opacity 250ms cubic-bezier(0.4, 0, 0.2, 1),
    visibility 250ms cubic-bezier(0.4, 0, 0.2, 1);

  @media screen and(min-width: 768px) {
    width: 500px;
  }

  @media screen and(min-width: 1200px) {
    top: 50%;
    transform: translate(-50%, -50%);
    width: 750px;
  }
}

.modal1.active {
  visibility: visible;
  opacity: 1;
  pointer-events: initial;
}

.modal2.active {
  visibility: visible;
  opacity: 1;
  pointer-events: initial;
}

.modal3.active {
  visibility: visible;
  opacity: 1;
  pointer-events: initial;
}

.modal__button {
  position: absolute;
  right: 15px;
  top: 15px;
  width: 40px;
  height: 40px;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 0;
  border: none;
  background-color: transparent;
  cursor: pointer;
}

.modal__icon {
  width: 40px;
  height: 40px;
  fill: black;

  .modal__button:focus &,
  .modal__button:active & {
    fill: #7eb101;
  }
}
.modal__text {
  font-size: 44px;
  line-height: calc(66 / 44);
  letter-spacing: 0.06em;
}

.backdrop {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.329);
  z-index: 41;
  visibility: hidden;
  opacity: 0;
  pointer-events: none;
  overflow: scroll;
  transition: opacity 250ms cubic-bezier(0.4, 0, 0.2, 1),
    visibility 250ms cubic-bezier(0.4, 0, 0.2, 1);
}

.backdrop.active {
  visibility: visible;
  opacity: 1;
  pointer-events: all;
}


/*button*/
.up-button {
  position: fixed;
  bottom: 45px;
  right: 45px;
}
.up-button__link {
  position: relative;
  top: -30px;
  left: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 12px;
  width: 45px;
  height: 80px;
  fill: #7eb101;
  &:hover,
  &:focus {
    scale: 1.2;
    background-color: #7fb10194;
    fill: #638230;
    box-shadow: rgb(77, 125, 64) 2px 0px 15px;
  }
}
.up-button__icon {
  position: relative;
  top: 20px;
  left: 0;
}

