.section__team {
  padding-top: 49px;
  padding-bottom: 59px;
}
.team__container {
  padding-left: 30px;
  padding-right: 30px;
}
.team__text {
  font-size: 20px;
  line-height: 1.65;
  text-align: center;
  margin-top: 0;
  margin-bottom: 16px;
}

.team__title {
  margin-bottom: 34px;
}

.section__descr {
  text-align: center;
  margin-top: 0;
  margin-bottom: 45px;
}
.team__list {
  @extend %list;
  // position: relative;

  display: flex;
  gap: 15px;
}

.team__name {
  font-weight: 400;
  font-size: 25px;
  line-height: 1.28;
  text-align: center;
  margin-top: 13px;
  margin-bottom: 12px;
}

.team__descr {
  text-align: center;
  margin-top: 0;
  margin-bottom: 0;
}

.soc__icon {
  fill: var(--secondary-color);
}

.team__item {
  position: relative;
}

.soc__list {
  @extend %list;
  display: flex;
  justify-content: center;
  gap: 25px;
  width: 100%;
  position: absolute;
  top: 130px;
  opacity: 0;
  transition: opacity 250ms cubic-bezier(0.4, 0, 0.2, 1);

  &:hover {
    opacity: 1;
  }
}

.team__img:hover ~ .soc__list {
  opacity: 1;
}

.team__item:not(:last-child) {
  margin-right: 25px;
}
